<template>
  <Transition name="nav" mode="out-in">
    <nav class="nav" v-if="!search">
      <Transition name="nav" mode="out-in">
        <button class="nav__lang" v-if="lang == 'ru'" @click="changeLang">
          Uz
          <img src="../assets/img/Uzbekistan-flag.webp" alt="" />
        </button>
        <button class="nav__lang" v-else @click="changeLang">
          Ru
          <img src="../assets/img/russia.png" alt="" />
        </button>
      </Transition>
      <div class="container">
        <h2 class="nav__title">{{langWord.appbartitle[lang]}}</h2>
      </div>
      <button class="nav__search" @click="$emit('changeNav', true)">
        <img src="../assets/img/search.svg" alt="" />
      </button>
    </nav>
    <nav class="nav" v-else>
      <button @click="back">
        <img src="../assets/img/back.svg" alt="" />
      </button>
      <div class="container">
        <input
          type="text"
          class="nav__input"
          :placeholder="langWord.appbarsearch[lang]"
          v-model="searchVal"
        />
      </div>
      <button @click="searchVal = ''">
        <img src="../assets/img/clear.svg" alt="" />
      </button>
    </nav>
  </Transition>
</template>
<script>
export default {
  data() {
    return {
      searchVal: "",
    };
  },
  props: {
    lang: String,
    langWord: Object,
    search: Boolean,
  },
  methods: {
    changeLang() {
      this.$emit("change", this.lang == "uz" ? "ru" : "uz");
    },
    back() {
      this.$emit("changeNav", false);
      this.searchVal = "";
    },
  },
  watch: {
    searchVal(value){
      this.$emit("searchVal",value)
    }
  },
};
</script>
<style lang="scss">
.nav-enter-active,
.nav-leave-active {
  transition: all 0.5s;
}

.nav-enter-from,
.nav-leave-to {
  transform: translateY(-20px);
  opacity: 0;
}
</style>
