<template >
  <div class="grid">
    <h2 class="grid__title" v-if="cards.lenght > 0">
      {{ langWord.infobar[lang] }}
    </h2>
    <h2 class="grid__title" v-else>{{ langWord.nonote[lang] }}</h2>
    <button
      class="grid__btn"
      v-if="!grid"
      @click="$emit('changeGrid', !grid)"
      v-show="cards.length > 0"
    >
      <img src="../assets/img/grid.svg" alt="" />
      {{ langWord.grid[lang] }}
    </button>
    <button
      class="grid__btn"
      v-else
      @click="$emit('changeGrid', !grid)"
      v-show="cards.length > 0"
    >
      <img src="../assets/img/list.svg" alt="" />
      {{ langWord.list[lang] }}
    </button>
  </div>
</template>
<script>
export default {
  props: {
    grid: Boolean,
    lang: String,
    langWord: Object,
    cards: Array,
  },
};
</script>
<style lang="scss">
.v-enter-active,
.v-leave-active {
  transition: all 0.5s;
}

.v-enter-from,
.v-leave-to {
  transform: translateX(20px);
  opacity: 0;
}
</style>
