<template >
  <div class="modal">
    <div class="modal__card">
      <h2 class="modal__title" v-if="add">{{langWord.editbtn[lang]}}</h2>
      <h2 class="modal__title" v-else>{{langWord.editbtn[lang]}}</h2>
      <label>
        Title
        <input type="text" placeholder="Title" v-model="title" />
      </label>
      <label>
         {{langWord.editbtn[lang]}}
        <input type="text" placeholder="Content" v-model="text" />
      </label>
      <div class="card__btn">
        <button class="card__btn-delete" @click="$emit('changeModal', false)">
          {{langWord.closebtn[lang]}} 
        </button>
        <button class="card__btn-edit" @click="addCard" v-if="add">
          {{langWord.addbtn[lang]}}
        </button>
        <button class="card__btn-edit" v-else @click="saveCard">
         {{langWord.editwindowbtn[lang]}}
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    add: Boolean,
    saveChange: Object,
    lang: String,
    langWord: Object,
  },
  data() {
    return {
      title: "",
      text: "",
    };
  },
  methods: {
    addCard() {
      if (this.title != "" && this.text != "") {
        this.$emit("addCard", {
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        });
        (this.title = ""), (this.text = "");
      }
    },
    saveCard() {
      if (this.title != "" && this.text != "") {
        this.$emit("saveCard", {
          id: this.saveChange.id,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        });
        (this.title = ""), (this.text = "");
      }
    },
  },
  mounted() {
    if (!this.add) {
      this.title = this.saveChange.title;
      this.text = this.saveChange.text;
    }
  },
};
</script>
<style lang="">
