<template>
  <Nav
    :lang="lang"
    :langWord="langWord"
    @change="lang = $event"
    :search="search"
    @changeNav="search = $event"
    @searchVal="searchInput = $event"
  />
  <div class="container">
    <Grid
      :lang="lang"
      :langWord="langWord"
      :grid="grid"
      @changeGrid="grid = $event"
      :cards="cards"
    />
    <Card
      :grid="grid"
      :cards="cardFilter"
      @changeCard="changeCard"
      :search="search"
      :lang="lang"
      :langWord="langWord"
    />
  </div>
  <Transition name="modal">
    <Modal
      :lang="lang"
      :langWord="langWord"
      v-if="modal"
      @changeModal="modal = $event"
      @addCard="addCard"
      :add="add"
      :saveChange="saveChange"
      @saveCard="saveCard"
    />
  </Transition>
  <AddBtn v-if="!modal" v-show="!search" @changeModal="modal = $event" />
</template>
<script>
import Modal from "./components/Modal";
import Nav from "@/components/Nav.vue";
import Grid from "@/components/Grid.vue";
import Card from "./components/Card.vue";
import AddBtn from "./components/AddBtn.vue";
import language from "./lang";
export default {
  components: {
    Modal,
    Nav,
    Grid,
    Card,
    AddBtn,
  },
  data() {
    return {
      lang: "uz",
      search: false,
      grid: true,
      modal: false,
      cards: [],
      id: 1,
      add: true,
      saveChange: {},
      searchInput: "",
      langWord: {},
    };
  },
  methods: {
    addCard(cardObj) {
      cardObj.id = this.id++;
      this.cards.push(cardObj);
      this.modal = false;
    },
    changeCard(cardId) {
      this.cards.forEach((card) => {
        if (card.id == cardId) {
          this.modal = true;
          this.add = false;
          this.saveChange = {
            id: card.id,
            title: card.title,
            text: card.text,
          };
        }
      });
    },
    saveCard(cardObj) {
      this.cards.forEach((card) => {
        if (cardObj.id == card.id) {
          card.title = cardObj.title;
          card.text = cardObj.text;
          card.time = cardObj.time;
          this.modal = false;
          this.add = true;
        }
      });
    },
  },
  computed: {
    cardFilter() {
      if (!this.search) return this.cards;
      let searchRes = this.searchInput.trim().toLowerCase();
      let array = this.cards.filter((card) => {
        if (card.title.toLowerCase().indexOf(searchRes) != -1) return card;
      });
      return array;
    },
  },
  created() {
    this.langWord = language;
    let localId = localStorage.getItem("id");
    let localCards = localStorage.getItem("cards");
    if(localCards){
      this.id = JSON.parse(localId);
    }
    if(localCards){
      this.cards = JSON.parse(localCards);
    }
  },
  watch:{
    id(){
      localStorage.setItem("id",JSON.stringify(this.id));
    },
    cards:{
      handler(array){
        localStorage.setItem("cards",JSON.stringify(array));
      },
      deep: true,
    }
  }
};
</script>
<style lang="scss">
.modal-enter-active,
.modal-leave-active {
  transition: all 0.5s;
}

.modal-enter-from,
.modal-leave-to {
  transform: scale(1.5);
  opacity: 0;
}
</style>